
.range-slider{position:relative;user-select:none;cursor:default;box-sizing:border-box
}
.range-slider *,.range-slider *:after,.range-slider *:before{box-sizing:inherit
}
.range-slider--horizontal{width:100px;height:6px
}
.range-slider--vertical{width:6px;height:100px
}
.range-slider__track{position:absolute;top:0;left:0;width:100%;height:100%;background:#333;overflow:hidden
}
.range-slider__filled{position:absolute;background:red;pointer-events:none;will-change:left, right
}
.range-slider--horizontal .range-slider__filled{height:100%
}
.range-slider--vertical .range-slider__filled{width:100%
}
.range-slider__control{position:absolute;background:#fff;cursor:pointer
}
.range-slider--horizontal .range-slider__control{top:50%;left:0;width:10px;height:20px;margin-top:-10px;margin-left:-5px
}
.range-slider--vertical .range-slider__control{top:0;left:50%;width:20px;height:10px;margin-top:-5px;margin-left:-10px
}
.range-slider__tooltip{position:absolute;display:block;padding:1em;line-height:1
}
.range-slider--horizontal .range-slider__tooltip{left:50%;text-align:center;transform:translate(-50%, 0)
}
.range-slider--vertical .range-slider__tooltip{top:50%;transform:translate(0, -50%)
}
.range-slider--horizontal .range-slider__tooltip--top{bottom:100%
}
.range-slider--vertical .range-slider__tooltip--top{left:100%;text-align:left
}
.range-slider--horizontal .range-slider__tooltip--bottom{top:100%
}
.range-slider--vertical .range-slider__tooltip--bottom{right:100%;text-align:right
}



